import React from "react";
import styled from "styled-components";
import { NavLink } from 'react-router-dom';
import './styleHeader.css'
const Menu = styled.div`
  width:100%;
  height:60px;
  display:flex;
  justify-content: space-between;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  background: #fff;
`;

function HeaderMenu() {
    return (
        <Menu>
            <div style={{lineHeight:'0.4' ,paddingLeft: '10px'}} className="Header_logo">
                <h1>Buget</h1>
            </div>
            <div>

            </div>
            <div style={{display:'flex',marginLeft:'250px'}} className="Header_content">
                <div>
                    <NavLink to="/" className='active'>Trang chủ</NavLink>
                </div>
                <div>
                    <NavLink to="/add" className='active'>Thêm chi tiêu</NavLink>
                </div>
                <div>
                    <NavLink to="/list" className='active'>Danh sách</NavLink>
                </div>
                <div>
                    <NavLink to="/note" className='active'>Ghi chú</NavLink>
                </div>

                
            </div>
            <div className="Header_avatar">
                {/* <div className="Header_avatar_img">
                    <p>Hello Thuyên</p>
                    <img style={{width:'40px',height:'40px',padding:'10px 10px',borderRadius:'50px'}} src="https://scontent.fhan19-1.fna.fbcdn.net/v/t39.30808-6/313004066_3138796453042420_2231190399642250537_n.jpg?_nc_cat=101&ccb=1-7&_nc_sid=09cbfe&_nc_ohc=h-uGJfARKs4AX8BuKUZ&_nc_ht=scontent.fhan19-1.fna&oh=00_AfDas-c6CPGObkwKch6Oy2trv3PTcqItB6_GouKsQuhvXA&oe=64A16DF9" alt="mylogo"></img>
                </div> */}
            </div>
        </Menu>
    )
}
export default HeaderMenu;