import React, { useState, useEffect } from "react";
import styled from "styled-components";

const Div2 = styled.div`
  width: 100%;
  height: 484px;
  background-color: #fff;
  border-bottom-left-radius: 20px;
  
`;

const NoteList = styled.ul`
  
  padding: 0;
  margin-top: 20px;
  width:80%;
  margin: 5px auto;
  font-size: 20px;
`;

const NoteItem = styled.li`
  // display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 10px;
`;

const NoteText = styled.span`
  margin-right: 10px;
`;

const DeleteButton = styled.button`
  background-color: #f44336;
  color: #fff;
  border: none;
  padding: 5px 10px;
  border-radius: 4px;
  cursor: pointer;
  float: right;
`;
const Button1 = styled.button`
  width: 100px;
  height: 40px;
  color: red;
  background: #badfe7;
  font-size: 20px;
  font-weight: bold;
  border-radius: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 5px auto;
`;

export default function AddExpress({ editExpense, setEditExpense }) {
  const [note, setNote] = useState("");
  const [noteData, setNoteData] = useState([]);

  const handleNgayChange = (event) => {
    setNote(event.target.value);
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    if (!note) {
      alert("Vui lòng nhập đầy đủ thông tin");
      return;
    }

    const newNote = {
      note: note,
    };

    const NoteData = JSON.parse(localStorage.getItem("NoteData")) || [];
    NoteData.unshift(newNote);
    localStorage.setItem("NoteData", JSON.stringify(NoteData));

    window.dispatchEvent(new Event("storage"));

    setNoteData(NoteData);
    setNote("");
  };

  const handleDelete = (index) => {
    const updatedNoteData = [...noteData];
    updatedNoteData.splice(index, 1);
    localStorage.setItem("NoteData", JSON.stringify(updatedNoteData));
    setNoteData(updatedNoteData);
  };

  useEffect(() => {
    const storedNoteData = JSON.parse(localStorage.getItem("NoteData")) || [];
    setNoteData(storedNoteData);
  }, []);

  return (
    <Div2>
      <p style={{ color: "red", textAlign: "center", fontSize: "20px" }}>
        Ghi Chú
      </p>
      <form onSubmit={handleSubmit} style={{ width: "50%",  margin: "0px auto" }}>
        <h3>Nhập nội dung</h3>
        <input
          type="text"
          placeholder=""
          value={note}
          onChange={handleNgayChange}
          style={{ width: '100%',height: '45px',borderRadius: '20px' }}
        />

        <br />
        <Button1 type="submit">Thêm</Button1>
      </form>

      <NoteList>
        {noteData.map((item, index) => (
          <NoteItem key={index}>
            <NoteText>{item.note}</NoteText>
            <DeleteButton onClick={() => handleDelete(index)}>Xóa</DeleteButton>
          </NoteItem>
        ))}
      </NoteList>
    </Div2>
  );
}



