import React from "react";

const TotalMoneyYear = ({ expenseList }) => {
  const getTotalExpense = () => {
    let total = 0;

    expenseList.forEach((expense) => {
      const { soTien } = expense;
      total += parseFloat(soTien);
    });

    return total;
  };

  const totalExpense = getTotalExpense();

  return (
    <div>
      <div style={{display:"flex", fontSize:'24px',paddingLeft:'10px',justifyContent:'space-between',padding:'0px 20px'}}>Tổng số tiền trong danh sách:

        <div style={{color:'red',fontSize:'30px',fontWeight:"bold"}}>
          {totalExpense} 
          <i class="fa-sharp fa-solid fa-dollar-sign"></i>
        </div>
      </div>
    </div>
  );
};

export default TotalMoneyYear;