
import React, { useState } from "react";
import styled from "styled-components";

const Div2 = styled.div`
  width:100%;
  height: 484px;
  background-color:#fff;
  border-bottom-left-radius: 20px;
`;

const Input1 = styled.input`
  width: 350px;
  height: 30px;
  border-radius: 5px;
`;
const Button1 = styled.button`
  width: 100px;
  height: 40px;
  color: red;
  background: #badfe7;
  font-size: 20px;
  font-weight: bold;
  border-radius: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 5px auto;
`;
export default function AddExpress({ editExpense, setEditExpense }) {
  const [ngay, setNgay] = useState("");
  const [thang, setThang] = useState("");
  const [danhMuc, setDanhMuc] = useState("");
  const [soTien, setSoTien] = useState("");
  const [noiDung, setNoiDung] = useState("");

  const handleNgayChange = (event) => {
    setNgay(event.target.value);
  };

  const handleThangChange = (event) => {
    setThang(event.target.value);
  };

  const handleDanhMucChange = (event) => {
    setDanhMuc(event.target.value);
  };

  const handleSoTienChange = (event) => {
    setSoTien(event.target.value);
  };

  const handleNoiDungChange = (event) => {
    setNoiDung(event.target.value);
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    // Kiểm tra các trường nhập liệu
  if (!ngay || !thang || !danhMuc || !soTien || !noiDung) {
    alert("Vui lòng nhập đầy đủ thông tin");
    return;
  }
    // Tạo object mới từ các giá trị nhập vào
    const newExpense = {
      ngay: ngay,
      thang: thang,
      danhMuc: danhMuc,
      soTien: soTien,
      noiDung: noiDung,
    };
    // Lưu vào local storage
    const expenseList = JSON.parse(localStorage.getItem("expenseList")) || [];
    expenseList.unshift(newExpense);
    localStorage.setItem("expenseList", JSON.stringify(expenseList));

    window.dispatchEvent(new Event("storage"));
    // Reset các giá trị nhập vào
    setNgay("");
    setThang("");
    setDanhMuc("");
    setSoTien("");
    setNoiDung("");
    
  };
  

  return (
    <Div2>
      <p style={{ color: "red", textAlign: "center",fontSize:'20px',fontWeight:'bold' }}>Thêm chi tiêu</p>
      <form onSubmit={handleSubmit} style={{display: 'grid',justifyContent: 'center',alignItems: 'center',justifyItems: 'stretch'}}>
        <p>Nhập ngày</p>
        <Input1
          type="number"
          placeholder="Nhập ngày"
          value={ngay}
          onChange={handleNgayChange}
        />

        <p>Nhập tháng</p>
        <Input1
          type="number"
          placeholder="Nhập tháng"
          value={thang}
          onChange={handleThangChange}
        />

        <br />
        <select value={danhMuc} onChange={handleDanhMucChange} style={{height:'20px',borderRadius:'5px'}}>
          <option>Chọn danh mục</option>
          <option>Ăn uống</option>
          <option>Học hành</option>
          <option>Gửi tiết kiệm</option>
        </select>

        <p>Nhập số tiền</p>
        <Input1
          type="number"
          placeholder="Nhập số tiền"
          value={soTien}
          onChange={handleSoTienChange}
        />

        <p>Nhập nội dung</p>
        <Input1
          type="text"
          placeholder="Nhập nội dung"
          value={noiDung}
          onChange={handleNoiDungChange}
        />
        <br />
        {/* <button type="submit" 
          style={{height:'30px',borderRadius:'10px',width:'100px',
          marginLeft:'100px',background: 'aqua',color: 'red',fontSize: '19px',fontWeight: 'bold'}}
        >Thêm
        </button> */}
        <Button1 type="submit">Thêm</Button1>
      </form>
      
    </Div2>
  );
}

