import React, { useEffect, useState } from "react";


const Chitieutungthang = ( ) => {
    const [localStorageUpdateCount, setLocalStorageUpdateCount] = useState(0);
  const [expenseList, setExpenseList] = useState([]);
  useEffect(() => {
    const storedExpenseList = JSON.parse(localStorage.getItem("expenseList")) || [];
    setExpenseList(storedExpenseList);
  }, [localStorageUpdateCount]);

  useEffect(() => {
    const updateLocalStorage = () => {
      setLocalStorageUpdateCount(prevCount => prevCount + 1);
    };
  
    window.addEventListener("storage", updateLocalStorage);
  
    return () => {
      window.removeEventListener("storage", updateLocalStorage);
    };
  }, []);
    const [selectedMonth, setSelectedMonth] = useState("");

  const totalByMonth = {};

  expenseList.forEach((expense) => {
    const { thang, soTien } = expense;
    if (totalByMonth[thang]) {
      totalByMonth[thang] += parseFloat(soTien);
    } else {
      totalByMonth[thang] = parseFloat(soTien);
    }
  });

  const handleMonthSelection = (event) => {
    const month = event.target.value;
    setSelectedMonth(month);
  };

  return (
    <div style={{marginLeft: '50px',display: 'flex',justifyContent: 'center',flexDirection: 'column'}} >
      <div style={{display:'flex',alignItems:'center',marginTop:'20px'}}>
        <h2>Thống kê chi tiêu </h2>
        
        <select style={{paddingLeft: '10px',height: '30px',borderRadius: '20px',marginLeft: '60px',marginTop:'7px'}} onChange={handleMonthSelection} value={selectedMonth}>
        
          
          <option value="">Chọn tháng</option>
          {Object.keys(totalByMonth).map((month) => (
            <option key={month} value={month}>
              Tháng {month}
            </option>
          ))}
        </select>
      </div>

      {selectedMonth && (
        <div>
          {/* <h3>Thống kê cho tháng {selectedMonth}:</h3> */}
          <h3 >Tổng số tiền trong tháng {selectedMonth}: {totalByMonth[selectedMonth]} <i  class="fa-sharp fa-solid fa-dollar-sign"></i> </h3>
        </div>
      )}
    </div>
  );
};

export default Chitieutungthang;