import React, { useEffect, useState } from "react";
import styled from "styled-components";
import EditExpenseForm from "./EditExpenseForm";
import TotalMoneyYear from "./TotalMoneyYear";



const Div1 = styled.div`
  width:100%;
  height:auto;
  background-color: rgb(255 255 255);
  
  border-bottom-left-radius: 20px;
`;
const Div2 = styled.div`

height:295px;
border-bottom-left-radius: 20px;

overflow: auto;
margin-bottom:40px
`;
const StyledTable = styled.table`
  width: 100%;
  border-collapse: collapse;
`;

const StyledTableHead = styled.thead`
  background-color: #f2f2f2;
`;

const StyledTableHeader = styled.th`
  padding: 10px;
  text-align: left;
`;

const StyledTableRow = styled.tr`
  &:nth-child(even) {
    background-color: #f9f9f9;
  }
`;

const StyledTableCell = styled.td`
  padding: 10px;
`;
const Btn1 = styled.button`
  background-color: rgb(202 126 21);
  color: rgb(255, 255, 255);
  border: none;
  padding: 5px 10px;
  border-radius: 4px;
  cursor: pointer;
`;

const Btn = styled.button`
  background-color: rgb(244, 67, 54);
  color: rgb(255, 255, 255);
  border: none;
  padding: 5px 10px;
  border-radius: 4px;
  cursor: pointer;
`;


export default function ListExpress() {
  const [expenseList, setExpenseList] = useState([]);
  const [localStorageUpdateCount, setLocalStorageUpdateCount] = useState(0);
  const [editingIndex, setEditingIndex] = useState(-1);

  useEffect(() => {
    const storedExpenseList = JSON.parse(localStorage.getItem("expenseList")) || [];
    setExpenseList(storedExpenseList);
  }, [localStorageUpdateCount]);
  
  useEffect(() => {
    const updateLocalStorage = () => {
      setLocalStorageUpdateCount(prevCount => prevCount + 1);
    };
  
    window.addEventListener("storage", updateLocalStorage);
  
    return () => {
      window.removeEventListener("storage", updateLocalStorage);
    };
  }, []);
  
  const handleEditExpense = (index) => {
    setEditingIndex(index);
  };

  const handleSaveExpense = (index, editedExpense) => {
    // Cập nhật expenseList mới
    const updatedExpenseList = [...expenseList];
    updatedExpenseList[index] = editedExpense;
    setExpenseList(updatedExpenseList);

    // Cập nhật lại localStorage
    localStorage.setItem("expenseList", JSON.stringify(updatedExpenseList));

    // Kết thúc chế độ chỉnh sửa
    setEditingIndex(-1);
  };
  
  const handleDeleteExpense = (index) => {
    // Xóa khoản chi tiêu từ expenseList dựa vào index
    const updatedExpenseList = expenseList.filter((_, i) => i !== index);
  
    // Cập nhật expenseList mới
    setExpenseList(updatedExpenseList);
  
    // Cập nhật lại localStorage
    localStorage.setItem("expenseList", JSON.stringify(updatedExpenseList));
  };
  
return (
  <>
  <Div1>
    
  {/* <TotalExpense expenseList={expenseList} /> */}
    <p style={{fontSize:'24px',color:'red',textAlign:'center'}}>Danh sách chi tiêu</p>
    <StyledTable>
    <StyledTableHead>
            <StyledTableRow>
              <StyledTableHeader>Ngày</StyledTableHeader>
              <StyledTableHeader>Danh mục</StyledTableHeader>
              <StyledTableHeader>Số tiền</StyledTableHeader>
              <StyledTableHeader>Nội dung</StyledTableHeader>
              <StyledTableHeader>Chức năng</StyledTableHeader>
            </StyledTableRow>
          </StyledTableHead>
      </StyledTable>
  <Div2>
        
        <StyledTable>
          {/* <StyledTableHead>
            <StyledTableRow>
              <StyledTableHeader>Ngày</StyledTableHeader>
              <StyledTableHeader>Danh mục</StyledTableHeader>
              <StyledTableHeader>Số tiền</StyledTableHeader>
              <StyledTableHeader>Nội dung</StyledTableHeader>
              <StyledTableHeader></StyledTableHeader>
            </StyledTableRow>
          </StyledTableHead> */}
          <tbody>
            {expenseList.map((expense, index) => (
              <StyledTableRow key={index}>
                {editingIndex === index ? (
                  <StyledTableCell colSpan="5">
                    <EditExpenseForm
                      expenseToEdit={expense}
                      onSave={(editedExpense) =>
                        handleSaveExpense(index, editedExpense)
                      }
                    />
                  </StyledTableCell>
                ) : (
                  <>
                    <StyledTableCell>{expense.ngay}/{expense.thang}</StyledTableCell>
                    <StyledTableCell style={{paddingLeft:'10px'}}>{expense.danhMuc}</StyledTableCell>
                    <StyledTableCell>{expense.soTien}</StyledTableCell>
                    <StyledTableCell>{expense.noiDung}</StyledTableCell>
                    <StyledTableCell >
                      <Btn1  onClick={() => handleEditExpense(index)}>
                        Sửa
                      </Btn1>
                      <Btn onClick={() => handleDeleteExpense(index)}>
                        Xóa
                      </Btn>
                    </StyledTableCell>
                  </>
                )}
              </StyledTableRow>
            ))}
          </tbody>
        </StyledTable>
      </Div2>

      <TotalMoneyYear expenseList={expenseList} ></TotalMoneyYear>
      
      


  </Div1>
  </>
);
}