import React, { useEffect, useState } from "react";
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend,
} from 'chart.js';
import { Bar } from 'react-chartjs-2';
// import faker from 'faker';

ChartJS.register(
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend
);

const MonthlyExpenseChart = () => {
    const [localStorageUpdateCount, setLocalStorageUpdateCount] = useState(0);
    const [expenseList, setExpenseList] = useState([]);
    useEffect(() => {
      const storedExpenseList = JSON.parse(localStorage.getItem("expenseList")) || [];
      setExpenseList(storedExpenseList);
    }, [localStorageUpdateCount]);
  
    useEffect(() => {
      const updateLocalStorage = () => {
        setLocalStorageUpdateCount(prevCount => prevCount + 1);
      };
    
      window.addEventListener("storage", updateLocalStorage);
    
      return () => {
        window.removeEventListener("storage", updateLocalStorage);
      };
    }, []);

  // Tạo một đối tượng để lưu trữ tổng số tiền theo tháng
  const totalByMonth = {};

  // Tính tổng số tiền theo tháng
  expenseList.forEach((expense) => {
    const { soTien,thang } = expense;
    if (totalByMonth[thang]) {
      totalByMonth[thang] += parseFloat(soTien);
    } else {
      totalByMonth[thang] = parseFloat(soTien);
    }
  });

  // Lấy danh sách các tháng và tổng số tiền tương ứng
  const months = Object.keys(totalByMonth);
  const amounts = Object.values(totalByMonth);

  
  // Tạo dữ liệu cho biểu đồ
  const chartData1 = {
    labels: months,
    datasets: [
      {
        label: "Tổng chi tiêu",
        data: amounts,
        backgroundColor: "rgba(75,192,192,0.4)",
        borderColor: "rgba(75,192,192,1)",
        borderWidth: 1,
      },
    ],
  };

  // Cấu hình tùy chọn cho biểu đồ
  const chartOptions = {
    scales: {
      y: {
        beginAtZero: true,
      },
    },
    
  };

  return (
    <div style={{display: 'grid', alignItems:'center', width: '50%', height:'350px', justifyContent: 'center'}}>
      {/* <h2>Biểu đồ tổng chi tiêu từng tháng</h2> */}
      <Bar data={chartData1} options={chartOptions} />
      
    </div>
  );
};

export default MonthlyExpenseChart;
