import React, { useEffect, useState } from "react";


const Phanloaitheomuc = () => {

    const [localStorageUpdateCount, setLocalStorageUpdateCount] = useState(0);
    const [expenseList, setExpenseList] = useState([]);
    useEffect(() => {
      const storedExpenseList = JSON.parse(localStorage.getItem("expenseList")) || [];
      setExpenseList(storedExpenseList);
    }, [localStorageUpdateCount]);
  
    useEffect(() => {
      const updateLocalStorage = () => {
        setLocalStorageUpdateCount(prevCount => prevCount + 1);
      };
    
      window.addEventListener("storage", updateLocalStorage);
    
      return () => {
        window.removeEventListener("storage", updateLocalStorage);
      };
    }, []);
      
  const getTotalByCategory = () => {
    const totalByCategory = {};

    expenseList.forEach((expense) => {
      const { danhMuc, soTien } = expense;
      if (totalByCategory[danhMuc]) {
        totalByCategory[danhMuc] += parseFloat(soTien);
      } else {
        totalByCategory[danhMuc] = parseFloat(soTien);
      }
    });

    return totalByCategory;
  };

  const totalByCategory = getTotalByCategory();

  return (
    <div>
      {/* <img style={{width:'77%',marginTop: '60px',marginLeft: '38px'}} src="https://banner2.cleanpng.com/20180217/xjq/kisspng-piggy-bank-finance-piggy-bank-5a88b5b5e27532.5043134815189088539276.jpg"></img> */}
      <img style={{width:'40%',marginTop: '15px',marginLeft: '70px'}} src="https://banner2.cleanpng.com/20180216/fxq/kisspng-piggy-bank-drawing-piggy-bank-5a87825b610897.3205106715188301713975.jpg"></img>
      
      <h2 style={{textAlign:'center',color:'rgb(0 209 209)'}}>Phân Loại</h2>
      <div style={{display:'block',height: '100px',margin: '0px 60px'}}>
        {Object.keys(totalByCategory).map((category) => (
          <div 
          style={{border: '1px solid #ddc7c7',width:'120px',height:'100px',
          textAlign:'center', borderRadius:'10px',backgroundColor: '#d0f4e8',margin:'40px 0px'}}  key={category}>
            <div>
              <p style={{padding:'0px 0px',color:'red'}}>
                {category}:
              </p> 
              <p>
                {totalByCategory[category]} 
                <i style={{paddingLeft:'5px'}} class="fa-sharp fa-solid fa-dollar-sign"></i>
                

              </p>
            </div>  
          </div>
        ))}
      </div>
      <div></div>
    </div> 
  );
};

export default Phanloaitheomuc;