import React, { useEffect, useState } from "react";
import { Chart as ChartJS, ArcElement, Tooltip, Legend } from 'chart.js';
import { Pie } from 'react-chartjs-2';

ChartJS.register(ArcElement, Tooltip, Legend);
const ExpenseChart = () => {
    const [localStorageUpdateCount, setLocalStorageUpdateCount] = useState(0);
    const [expenseList, setExpenseList] = useState([]);
    useEffect(() => {
      const storedExpenseList = JSON.parse(localStorage.getItem("expenseList")) || [];
      setExpenseList(storedExpenseList);
    }, [localStorageUpdateCount]);
  
    useEffect(() => {
      const updateLocalStorage = () => {
        setLocalStorageUpdateCount(prevCount => prevCount + 1);
      };
    
      window.addEventListener("storage", updateLocalStorage);
    
      return () => {
        window.removeEventListener("storage", updateLocalStorage);
      };
    }, []);
  // Tạo một đối tượng để lưu trữ tổng số tiền theo loại chi tiêu
  const totalByCategory = {};
  let total = 0;
  expenseList.forEach((expense) => {
    const { soTien } = expense;
    total += parseFloat(soTien);
  });

  // Tính tổng số tiền theo loại chi tiêu
  expenseList.forEach((expense) => {
    const { danhMuc, soTien } = expense;
    if (totalByCategory[danhMuc]) {
      totalByCategory[danhMuc] += parseFloat(soTien);
      
    } else {
      totalByCategory[danhMuc] = parseFloat(soTien);
    }
  });
  const percentages = {};
  for (const category in totalByCategory) {
    const amount = totalByCategory[category];
    const percentage = (amount / total) * 100;
    percentages[category] = percentage.toFixed(2);
  }

  // Lấy danh sách các loại chi tiêu và tổng số tiền tương ứng
  const categories = Object.keys(totalByCategory);
  const amounts = Object.values(totalByCategory);
  

  // Tạo dữ liệu cho biểu đồ
  const chartData = {
    labels: categories,
    datasets: [
      {
        data: amounts,
        backgroundColor: [
          "#FF6384",
          "#36A2EB",
          "#FFCE56",
          // Các màu khác tùy ý
        ],
        hoverBackgroundColor: [
          "#FF6384",
          "#36A2EB",
          "#FFCE56",
          // Các màu khác tùy ý
        ],
      },
    ],
  };
//   Hiển thị phần trăm trên biểu đồ
const chartOptions = {
  plugins: {
    tooltip: {
      callbacks: {
        label: (context) => {
          const label = context.label || "";
          const percentage = percentages[label] || 0;
          return `${label}:${percentage}%`;
        },
      },
    },
  },
};

  return (
    <div style={{display: 'grid', alignItems:'center', width: '50%', height:'350px', justifyContent: 'center'}}>
      {/* <h2>Biểu đồ phần trăm tổng chi tiêu theo loại:</h2> */}
      <Pie data={chartData} options={chartOptions}  />
      {/* <p style={{textAlign:'center'}}>biểu đồ chi tiêu thoe tháng</p> */}

    </div>
  );
}
  export default ExpenseChart;