import React, { useState } from "react";

export default function EditExpenseForm({ expenseToEdit, onSave }) {
  const [editedExpense, setEditedExpense] = useState(expenseToEdit);

  const handleChange = (event) => {
    const { name, value } = event.target;
    setEditedExpense((prevState) => ({
      ...prevState,
      [name]: value,
    }));
  };

  const handleSave = () => {
    onSave(editedExpense);
  };

  return (
    <div>
      <label>nhap lai ngay</label>
      <input
        type="text"
        name="ngay"
        value={editedExpense.ngay}
        onChange={handleChange}
      />
        <br></br>
        <label>nhap lai tháng</label>
      <input
        type="text"
        name="thang"
        value={editedExpense.thang}
        onChange={handleChange}
      /> 
      <br></br>
      <label>chọn lại danh mục</label>
      <select
        name="danhMuc"
        value={editedExpense.danhMuc}
        onChange={handleChange}
      >
        <option value="">Chọn danh mục</option>
        <option value="Ăn uống">Ăn uống</option>
        <option value="Học hành">Học hành</option>
        <option value="Gửi tiết kiệm">Gửi tiết kiệm</option>
      </select>

      <br></br>
      <label>nhập lại số tiền</label>
      <input
        type="text"
        name="soTien"
        value={editedExpense.soTien}
        onChange={handleChange}
      />
      <br></br>
      <label>Viết lại nội dung</label>
      <input
        type="text"
        name="noiDung"
        value={editedExpense.noiDung}
        onChange={handleChange}
      />
      <button onClick={handleSave}>Lưu</button>
    </div>
  );
}




